.fullscreen {
    position: relative;
    width: 100vw;
    height: 100vh;
}

.home {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-template-rows: 1fr 1fr .1fr;
    height: 100vh;
    width: 100%;
    grid-template-areas: 
        'topcont top1 top2'
        'bottomcont moreabout bottom1'
        'footer footer footer';
}

.home .topcont {
    grid-area: topcont;
    text-align: center;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: 
        't'
        'm'
        'b';
}

.home .topcont h1{
    font-size: 5em;
}

.home .topcont .mid {
    grid-area: m;
}

.home .topcont .bot {
    grid-area: b;
}

.home .top1 {
    grid-area: top1;
    background-color: #d72323;
    color: black;
    transition: 0.3s;
    font-size: 50px;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'logo'
        'text';
}

.home .top1:hover {
    background-color: black;
    color: #d72323;
}

.home .top2 {
    grid-area: top2;
    text-align: center;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'top'
        'bottom';
}

.home .top2 .topLink {
    grid-area: top;
    font-size: 50px;
    color: #f5eded;
}

.home .top2 .topLink:hover {
    background-color: #f5eded;
    color: black;
}

.home .top2 .bottomLink {
    grid-area: bottom;
    font-size: 50px;
    color: #f5eded;
}

.home .top2 .bottomLink:hover {
    background-color: #f5eded;
    columns: black;
}

.home .bottomcont {
    grid-area: bottomcont;
    text-align: center;
    display: grid;
    grid-template-rows: .75fr 1fr .75fr;
    grid-template-areas: 
        'contenttop'
        'contentmid'
        'contentbot';
}

.home .bottomcont .content {
    grid-area: contentmid;
    color: #888888;
}


.home .moreabout {
    grid-area: moreabout;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'about'
        'cv';
}


.home .moreabout .about {
    grid-area: about;
    font-size: 50px;
    transition: .3s;
}

.home .moreabout .cv {
    grid-area: cv;
    font-size: 50px;
}

.home .moreabout .cv a {
    text-decoration: none;
    color: #f5eded;
}

.home .moreabout .about:hover {
    background-color: #f5eded;
    color: black;
}

.home .moreabout .cv:hover {
    background-color: #f5eded;
    color: black;
    transition: .3s;
}

.home .bottom1 {
    grid-area: bottom1;
    background-color: #f5eded;
    color: black;
    transition: .3s;
    font-size: 50px;
}

.home .bottom1:hover {
    background-color: black;
    color: #f5eded;
}

.home footer {
    grid-area: footer;
    text-align: center;
    background-color: #3e3636;
    display: grid;
    grid-template-rows: .75fr 1fr .75fr;
    grid-template-areas: 
        'footertop'
        'footermid'
        'footerbot';
}

.home .footer {
    grid-area: footermid;
}

.page {
    background-color: #000000;
    color: white;
    font-family: 'Comfortaa';
}